/* eslint-disable no-alert */
/* eslint-disable react/prop-types */
/* eslint-disable react/no-multi-comp */
/* eslint-disable jsx-a11y/href-no-hash */
/* eslint-disable jsx-a11y/label-has-for */
import React, { Component } from 'react';
import 'drip-form-validator/lib/locale/ja'; // eslint-disable-line import/no-extraneous-dependencies
import { dripForm, Validator } from '../../../../src/';
import { Layout, Button, Code } from '../../components/';
import { Input, Textarea } from '../../fields/';


const I18nForm = dripForm({
  validations: {
    email: {
      required: true,
      email: true,
    },
    message: {
      required: true,
    },
  },
})(({
  handlers,
  meta: { invalid, pristine },
}) => (
  <form onSubmit={handlers.onSubmit}>
    <div>
      <label htmlFor="email">送信先</label>
      <Input
        id="email"
        type="email"
        name="email"
        label="送信先"
      />
    </div>

    <div>
      <label htmlFor="message">メッセージ</label>
      <Textarea
        id="message"
        name="message"
        label="メッセージ"
      />
    </div>

    <Button
      primary
      onClick={handlers.onSubmit}
      disabled={invalid || pristine}
    >
      送信
    </Button>
  </form>
));


export default class BasicFormExample extends Component {
  state = {
    values: {},
  };

  componentWillMount() {
    Validator.setLocale('ja');
  }

  componentWillUnmount() {
    Validator.setLocale('en');
  }

  render() {
    const { location } = this.props;
    const { values } = this.state;

    return (
      <Layout
        title="i18n"
        location={location}
      >
        <p>
          todo...<br />
          <a href="https://github.com/yahoo/react-intl">yahoo/react-intl</a>
        </p>

        <hr />

        <h3>Example:</h3>
        <I18nForm
          onChange={v => this.setState({ values: v })}
          onValidSubmit={(v) => {
            alert('See console');
            console.log(v);
          }}
        />
        <hr />

        <h3>Values:</h3>
        <Code language="javascript">{JSON.stringify(values, null, 2)}</Code>
        <hr />

        <h3>Sample Code:</h3>
        <Code language="javascript">{`import React, { Component } from 'react';
import { dripForm } from 'react-drip-form';
`}</Code>
      </Layout>
    );
  }
}



